<template>
  <div id="new-employee">
    <h3>New Employee</h3>
    <div class="row">
      <form @submit.prevent="saveEmployee" class="">
        <div class="field">
          <div class="control">
            <input class="input is-primary" type="text" placeholder="ID" v-model="employee_id">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input class="input is-primary" type="text" placeholder="Name" v-model="name">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input class="input is-primary" type="text" placeholder="dept" v-model="dept">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input class="input is-primary" type="text" placeholder="position" v-model="position">
          </div>
        </div>
        <button type="submit" class="button is-pro=imary">save</button>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-employee',
  data () {
    return {
      name: null,
      employee_id: null,
      dept: null,
      position: null
    }
  },
  methods: {
    saveEmployee () {
      db.collection('employees')
        .add({
          emp_id: this.employee_id,
          name: this.name,
          dept: this.dept,
          position: this.position
        }).then(docRef => this.$router.push('/'))
        .catch(error => console.log(error))
    }
  }
}
</script>

<style scoped>

</style>



