<template>
  <div id="view-employee">
    <section class="hero">
      <div class="hero-body">
        <div class="container">
          <h1 class="title">
            {{name}}
          </h1>
          <h2 class="subtitle">
            {{dept}}
          </h2>
          <p>{{employee_id}} : {{position}}</p>
        </div>
      </div>
    </section>
    <router-link to="/" class="button is-primary">Back</router-link>
    <router-link v-bind:to="{name: 'edit-employee', params: { employee_id: employee_id }}" class="button is-warning">edit</router-link>
    <button @click="deleteEmployee" class="button is-danger">Delete</button>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-employee',
  data () {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('employees').where('emp_id', '==', to.params.employee_id).get().then(snapshot => {
      snapshot.forEach(doc => {
        next(vm => {
          vm.employee_id = doc.data().emp_id
          vm.name = doc.data().name
          vm.dept = doc.data().dept
          vm.position = doc.data().position
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('employees').where('emp_id', '==', this.$route.params.employee_id).get().then(snapshot => {
        snapshot.forEach(doc => {
          this.employee_id = doc.data().emp_id
          this.name = doc.data().name
          this.dept = doc.data().dept
          this.position = doc.data().position
        })
      })
    },
    deleteEmployee () {
      if (confirm('Are you sure?')) {
        db.collection('employees').where('emp_id', '==', this.$route.params.employee_id).get().then(snapshot => {
          snapshot.forEach(doc => {
            doc.ref.delete()
            this.$router.push('/')
          })
        })
      }
    }
  }
}
</script>

<style scoped>

</style>



