<template>
  <div id="dashboard">
    <h3>Dashboard</h3>
    <p class="control field">
            <router-link to="/new" class="button is-primary">
              <span>
                add employee
              </span>
            </router-link>
          </p>
    <table class="table is-bordered is-striped is-narrow is-hoverable is-fullwidth">
      <thead>
        <tr>
          <th>id</th>
          <th>name</th>
          <th>dept</th>
          <th>position</th>
          <th>action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="employee in employees" v-bind:key="employee.id">
          <td>{{employee.emp_id}}</td>
          <td>{{employee.name}}</td>
          <td>{{employee.dept}}</td>
          <td>{{employee.position}}</td>
          <td>
            <router-link v-bind:to="{name: 'view-employee', params: { employee_id: employee.emp_id }}">view</router-link>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      employees: []
    }
  },
  created () {
    db
      .collection('employees')
      .orderBy('emp_id')
      .get()
      .then(snapshot => {
        snapshot.forEach(doc => {
          console.log(doc.data())
          this.employees.push({
            id: doc.id,
            name: doc.data().name,
            emp_id: doc.data().emp_id,
            dept: doc.data().dept,
            position: doc.data().position
          })
        })
      })
  }
}
</script>

<style scoped>

</style>



