<template>
  <div id="edit-employee">
    <h3>Edit Employee</h3>
    <div class="row">
      <form @submit.prevent="updateEmployee" class="">
        <div class="field">
          <div class="control">
            <input class="input is-primary" type="text" placeholder="ID" v-model="employee_id">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input class="input is-primary" type="text" placeholder="Name" v-model="name">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input class="input is-primary" type="text" placeholder="dept" v-model="dept">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input class="input is-primary" type="text" placeholder="position" v-model="position">
          </div>
        </div>
        <button type="submit" class="button is-primary">save</button>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-employee',
  data () {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('employees').where('emp_id', '==', to.params.employee_id).get().then(snapshot => {
      snapshot.forEach(doc => {
        next(vm => {
          vm.employee_id = doc.data().emp_id
          vm.name = doc.data().name
          vm.dept = doc.data().dept
          vm.position = doc.data().position
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('employees').where('emp_id', '==', this.$route.params.employee_id).get().then(snapshot => {
        snapshot.forEach(doc => {
          this.employee_id = doc.data().emp_id
          this.name = doc.data().name
          this.dept = doc.data().dept
          this.position = doc.data().position
        })
      })
    },
    updateEmployee () {
      db.collection('employees').where('emp_id', '==', this.$route.params.employee_id).get().then(snapshot => {
        snapshot.forEach(doc => {
          doc.ref.update({
            emp_id: this.employee_id,
            name: this.name,
            dept: this.dept,
            position: this.position
          }).then(() => {
            this.$router.push({ name: 'view-employee',
              params: { employee_id: this.employee_id }})
          })
        })
      })
    }
  }
}
</script>

<style scoped>

</style>



