<template>
  <div id="navbar">
   <nav class="navbar is-transparent is-link">
  <div class="navbar-brand">
    <a class="navbar-item">
      <img src="https://bulma.io/images/bulma-logo.png" alt="Bulma: a modern CSS framework based on Flexbox" width="112" height="28">
    </a>
    <div class="navbar-burger burger" data-target="navbarExampleTransparentExample">
      <span></span>
      <span></span>
      <span></span>
    </div>
  </div>

  <div id="navbarExampleTransparentExample" class="navbar-menu">
    <div class="navbar-start">
      <router-link to="/" class="navbar-item">
        Home
      </router-link>
      <router-link to="/" v-if="isLoggedIn" class="navbar-item">
        Dashboard
      </router-link>
    </div>

    <div class="navbar-end">
      <router-link to="/" v-if="isLoggedIn" class="navbar-item">
        {{currentUser}}
      </router-link>
      <div class="navbar-item">
        <div class="field is-grouped">
          <p class="control" v-if="isLoggedIn">
            <button @click="logout" class="button is-primary">
              <span>
                Logout
              </span>
            </button>
          </p>
          <p class="control">
            <router-link to="/register" v-if="!isLoggedIn" class="button is-primary">
              <span>
                Register
              </span>
            </router-link>
          </p>
          <p class="control">
            <router-link to="/login" v-if="!isLoggedIn" class="button is-primary">
              <span>
                Login
              </span>
            </router-link>
          </p>
        </div>
      </div>
    </div>
  </div>
</nav>
  </div>
</template>

<script>
import firebase from 'firebase'
export default {
  name: 'navbar',
  data () {
    return {
      isLoggedIn: false,
      currentUser: false
    }
  },
  created () {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true
      this.currentUser = firebase.auth().currentUser.email
    }
  },
  methods: {
    logout: function () {
      firebase.auth().signOut().then(() => {
        this.$router.go({ path: this.$router.path })
      })
    }
  }
}
</script>



